import React, { useState } from 'react';
import ReactDOM from 'react-dom';


const KakeiboTable = () => {
    const [posts, setPosts] = useState({posts});
    const [category_totals, setCategoryTotals] = useState({category_totals});
    const category_names = ['食費','交通費','趣味','日用品','交際費','衣服・美容','その他'];

    return (
        <>
            <table className="top_detail">
                <tr>
                    <th className="category">カテゴリ</th>
                    <th className="total">合計額</th>
                </tr>
                {posts.map(post => (
                    <tr key={post.category_id}>
                        <td>
                            <a href={`/kb/kakeibo/detail/${post.category_id}`}>{category_names[post.category_id-1]}</a>
                        </td>
                        <td>
                            {category_totals[post.category_id]}円
                        </td>
                   
                    </tr>
                ))}
            </table>
        </>
    );
}

export default KakeiboTable;

ReactDOM.render(
    <KakeiboTable posts={posts}  category_totals={category_totals} />,
    document.getElementById('category-totals-container') 
);